<template>
<div>
    <div class="container">
        
        <div>
            <h3><i class="fa fa-chart-line"></i> Reporte de Pedidos Reagendados</h3>
        </div>

        <hr>
        
        <!-- Rango de fechas -->
        <b-row>
            <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
                <label>Desde:</label>
                <b-form-datepicker v-model="desde" class="mb-2" locale="es" size="sm"></b-form-datepicker>
            </div>
            <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
                <label>Hasta:</label>
                <b-form-datepicker v-model="hasta" class="mb-2" locale="es" size="sm"
                    ></b-form-datepicker>
            </div>
        </b-row>

        <!-- Clientes y Vendedores -->
        <div class="row">

            <!-- Vendedores -->
            <div class="form-group col-sm-12 col-md-4 col-lg-3 mb-2">
                <label>Vendedor:</label>
                <div class="input-group">
                    <select v-model="vendedor"
                            class="form-control"
                            @change="changeVendedor">
                        <option v-for="vendedor in vendedores " 
                                v-bind:key="vendedor.value"
                                v-bind:value="vendedor"
                                >  {{vendedor.text}}
                        </option>
                    </select>
                </div>
            </div>

            <!-- Cliente -->
            <div class="form-group col-sm-12 col-md-4 col-lg-3 mb-2">
                <label>Cliente:</label>
                <div class="input-group">
                    <select v-model="cliente"
                            class="form-control"
                            :disabled = bloqueado
                            @change="changeCliente">
                        <option v-for="cliente in clientes " 
                                v-bind:key="cliente.value"
                                v-bind:value="cliente"
                                >  {{cliente.text}}
                        </option>
                    </select>
                </div>
            </div>
            
        </div>

        <hr>

        <div>

            <!-- botones -->
            <b-row>
                <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
                    <b-button @click="BuscarDatosReportePedidosReagendados" block variant="primary" class="mr-2 mt-2">
                        <i class="fas fa-search"></i> Buscar Datos
                    </b-button>
                </b-col>
                
                <b-col class="col-md-3 col-sm-12 col-12" align-self="end" v-if="datos">
                    <b-button @click="GenerarReportePedidosReagendados" block variant="dark" class="mr-2 mt-2">
                        <i class="fas fa-chart-bar"></i> Generar Reporte
                    </b-button>
                </b-col>

                <b-col class="col-md-3 col-sm-12 col-12" align-self="end" v-if="datos">
                    <b-button @click="exportarExcel" block variant="success" class="mr-2 mt-2">
                        <i class="fas fa-file-excel"></i> Exportar Excel
                    </b-button>
                </b-col>
            </b-row>

            <!-- Datos -->
            <div v-if="datos">
                <hr>
                <b-row class="my-2">
                    <b-col md="4">
                        <b-form-group label="Filtrar"
                                    label-cols-sm="1"
                                    label-cols-md="2"
                                    label-size="sm"
                                    label-for="filterInput">
                            <b-input-group size="sm">
                                <b-form-input v-model="filter"
                                                type="search"
                                                id="filterInput"
                                                placeholder="Escribe el filtro..."
                                                autocomplete="off">
                                </b-form-input>
                                <b-input-group-append>
                                    <b-button :disabled="!filter" @click="filter = ''">
                                        <i class="fas fa-times"></i>
                                    </b-button>
                                </b-input-group-append>
                            </b-input-group>
                        </b-form-group>
                    </b-col>
                    <b-col md="3">
                        <b-form-group label="Por página"
                                        label-cols-sm="2"
                                        label-cols-md="2"
                                        label-cols-lg="4"
                                        label-size="sm"
                                        label-for="perPageSelect">
                            <b-form-select v-model="perPage"
                                            id="perPageSelect"
                                            size="sm"
                                            :options="pageOptions">
                            </b-form-select>
                        </b-form-group>
                    </b-col>
                </b-row>
                <!-- Main table element -->
                <b-table small
                        stacked="md"
                        head-variant="dark"
                        :hover="hover"
                        :striped="striped"
                        :bordered="bordered"
                        :items="items"
                        :fields="fields"
                        :current-page="currentPage"
                        :per-page="perPage"
                        :filter="filter"
                        :filterIncludedFields="filterOn"
                        :sort-by.sync="sortBy"
                        :sort-desc.sync="sortDesc"
                        :sort-direction="sortDirection"
                        @filtered="onFiltered">
                    <template v-slot:cell(motivo)="row">
                        <b-button size="sm" v-b-tooltip.hover title="Ver Motivo Cancelación" variant="warning"
                                    @click="mostrarMotivo(row.item.motivo)">
                            <i class="fa fa-eye"></i>
                        </b-button>
                    </template>
                </b-table>
                
                <b-pagination v-model="currentPage"
                                class="mt-1"
                                :total-rows="totalRows"
                                :per-page="perPage"
                                align="center"
                                aria-controls="my-table">
                </b-pagination>
            </div>
            
        </div>
    </div>
</div>
</template>

<script>

import servicioClientes from "@/services/servicio-clientes";
import servicioVendedores from "@/services/servicio-vendedores";
import servicioReportes from '@/services/servicio-reportes';

export default {
    data() {

        const now = new Date()
        const last = new Date(now.getFullYear(), now.getMonth()-1, now.getDate())
        const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())

        var lastDate = last.toISOString().slice(0,10);
        var todayDate = today.toISOString().slice(0,10);

        return {
            desde: lastDate,
            hasta: todayDate,
            cliente: 0,
            clientes: [{ value: 0, text: "Todos" }],

            vendedor: 0,
            vendedores: [{ value: 0, text: "Todos" }],
            bloqueado: true,

            //
            datos: false,
            items: [],
            totalRows: 1,
            currentPage: 1,
            perPage: 5,
            pageOptions: [5, 10, 15],
            sortBy: "",
            sortDesc: false,
            bordered: true,
            hover: true,
            striped: true,
            sortDirection: "asc",
            filter: null,
            filterOn: [],
            fields: [
                {
                    key: "folioPedido",
                    label: "Folio",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "cliente",
                    label: "Cliente",
                    sortable: true,
                    sortDirection: "desc",
                },
                {
                    key: "vendedor",
                    label: "Vendedor",
                    sortable: true,
                    sortDirection: "desc",
                },
                {
                    key: "cantidadFormato",
                    label: "Cantidad",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "fechaAnteriorFormato",
                    label: "Fecha Anterior",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "fechaNuevaFormato",
                    label: "Fecha Nueva",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "motivo",
                    label: "Motivo",
                    class: "text-center",
                },
                
            ],

        }

    },
    components: {
    },
    async mounted() {
        this.cliente = this.clientes[0];
        await this.ObtenerVendedores(0);
    },
    created() {
        
    },
    computed: {
        sortOptions() {
            // Create an options list from our fields
            return this.fields
                        .filter((f) => f.sortable)
                        .map((f) => {
                            return { text: f.label, value: f.key };
                        });
        },
    },
    methods: {

        async ObtenerVendedores(vendedor) {
            this.$loading(true);
            await 
            servicioVendedores.obtenerVendedores(vendedor)
                .then((r) => {
                    
                    if (r.value) {

                        r.data.forEach((element) => {
                            let vendedor = {
                                value: element.codigo,
                                text: element.nombreCompleto,
                            };
                            this.vendedores.push(vendedor);
                        });

                        this.vendedor = this.vendedores[0];
                    }
                });
                
                this.$loading(false);
        },

        async BuscarDatosReportePedidosReagendados() {
            this.items = [];
            this.$loading(true);
            let result = await servicioReportes.ObtenerReportePedidosReagendados(this.desde, this.hasta, this.cliente.value, this.vendedor.value);
            if(result.value) {
                this.datos = true;
                this.items = result.data;
                this.CargarRenglones();
                this.$loading(false);
            } else {
                this.datos = false;
                this.$loading(false);
                this.$swal.fire({
                    text: result.message,
                    icon: "warning"
                });
            }
        },

        async GenerarReportePedidosReagendados(){
            this.$loading(true);
            let result = await servicioReportes.GenerarReportePedidosReagendados(this.desde, this.hasta, this.vendedor.value, this.cliente.value, this.items);
            if(result.value) {
                await this.ImprimirReportePedidosReagendados(result.data);
                this.$loading(false);
                const Toast = this.$swal.mixin({
                    toast: true,
                    position: 'top-end',
                    showConfirmButton: false,
                    timer: 3000,
                    timerProgressBar: true,
                    didOpen: (toast) => {
                        toast.addEventListener('mouseenter', this.$swal.stopTimer)
                        toast.addEventListener('mouseleave', this.$swal.resumeTimer)
                    }
                })

                Toast.fire({
                    icon: 'success',
                    title: 'Reporte de pedidos Reagendados descargado.'
                });
            } else {
                this.$loading(false);
                this.$swal.fire({
                    text: result.message,
                    icon: "warning"
                });
            }
        },
        
        async ImprimirReportePedidosReagendados(pdfResult) {
            const downloadLink = document.createElement("a");
            const fileName = "Reporte de Pedidos Reagendados";
            downloadLink.href = "data:application/pdf;base64," + pdfResult;
            downloadLink.download = fileName;
            await downloadLink.click();
        },

        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
        },

        CargarRenglones() {
            setTimeout(() => {
            this.totalRows = this.items.length;
            }, 1000);
        },

        async changeVendedor() {
            if(this.bloqueado) {
                this.bloqueado = false;
            }
            this.datos = false;
            this.items = [];
            this.clientes = [{ value: 0, text: "Todos" }];

            this.$loading(true);
            await 
            servicioClientes.obtenerClientesAgente(this.vendedor.value)
                .then((r) => {

                    if (r.value) {

                        r.data.forEach((element) => {
                            let cliente = {
                                value: element.codigo,
                                text: element.nombreCompleto,
                            };
                            this.clientes.push(cliente);
                        });

                        this.cliente = this.clientes[0];
                    }
                });
                
                this.$loading(false);
            // if(this.bloqueado) {
            //     this.bloqueado = false;
            // } else {
            //     this.datos = false;
            //     this.items = [];
            // }
        },

        changeCliente() {
            this.datos = false;
            this.items = [];
        },

        exportarExcel() {
            this.$loading(true);
            let csv = Object.keys(this.items[0]).join("♪") + "\n"; //headers
                            
            for (var item in this.items) {
                csv += Object.values(this.items[item]).join("♪");
                csv += "\n";
            }
            csv = csv.replaceAll(',', ' ')
            csv = csv.replaceAll('♪', ',')
            
            const blob = new Blob([csv], { type: 'text/csv' });
            const url = window.URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.setAttribute('hidden', ''); 
            a.setAttribute('href', url);
            a.setAttribute('download', 'download.csv');
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
            this.$loading(false);
        },

        mostrarMotivo(motivo) {

            this.$swal.fire({
              text: motivo,
              icon: "info",
            });

        }

    }
   
}
</script>

<style scoped>

</style>
